@font-face {
  font-family: "Digital";
  src: url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2")
      format("woff2"),
    url("//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7")
      format("svg");
}

body {
  background-color: black;
  color: white;
  font-family: Acme, sans-serif;
}
#time-left {
  font-family: "Digital", sans-serif;
  font-weight: bold;
  font-size: 100px;
  width: 225px;
  color: #ffd700;
  margin: 0 auto;
}
#title,
#timer-label {
  text-align: center;
  font-size: 35px;
}
#timer-label {
  font-size: 30px;
}

#break-label,
#break-length,
#break-button,
#break-left,
#session-left,
#play {
  float: left;
}
#break-label {
  font-size: 20px;
  position: relative;
  left: 300px;
}
#session-label {
  font-size: 20px;
  position: relative;
  right: 300px;
}
#break-button {
  font-size: 25px;
  position: relative;
  left: 300px;
  bottom: 115px;
}
#session-button {
  font-size: 25px;
  position: relative;
  right: 300px;
  bottom: 115px;
}
#session-label,
#session-length,
#session-button,
#break-right,
#session-right,
#reset {
  float: right;
}
#label-title,
#lengths,
#break-button,
#session-button {
  clear: left;
  clear: right;
}
#play-reset {
  margin: 0 auto;
  width: 100vw;
}
#break-length {
  position: relative;
  left: 340px;
  bottom: 55px;
  font-size: 40px;
}
#session-length {
  position: relative;
  right: 340px;
  bottom: 55px;
  font-size: 40px;
}
#break-right {
  position: relative;
  left: 60px;
}
#session-left {
  position: relative;
  right: 60px;
}
#play {
  position: relative;
  left: 590px;
}
#reset {
  position: relative;
  right: 300px;
}
